window.onload = async function () {
  logoutBtnOnClick(XANO_ADMIN_SIGN_IN_REDIRECT, "sidebar-logout-btn");

 const token = getToken();
  if (token) {
    let result = await fetchUrl(XANO_SUPER_ADMIN_ALL_SESSIONS_URL, "GET", false, token);

    if (result.isFetchOk !== undefined && !result.isFetchOk) {
      console.error(result.message);
      if (
        result.payload &&
        result.payload.role === ROLES.ADMIN &&
        result.payload.gyms &&
        result.payload.gyms.length > 0
      )
        redirectTo(`/admin/gym/g?gymCode=${result.payload.gyms[0].gym_id}`);
      else redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=UNAUTHORIZED_USER`);
      return;
    }

    assignAdminNavbar(ROLES.SUPER_ADMIN);
    getSuperAdminData(result, token);
  } else {
    // ? if there is no token
    redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
  }
};

